.user-list {
  @include list-reset;
  width: 100%;
}

.user-list__login {
  display: block;
  padding: 10px 0;

  width: 270px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .user-list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    order: -1;
    width: 333px;
  }

  .user-list__item {
    margin-left: 143px;
  }

  .user-list__login {
    width: 190px;
  }
}

@media (min-width: $desktop-width) {
  .user-list {
    display: flex;
    width: 190px;
    order: 1;
  }

  .user-list__item {
    margin-left: 0;
  }
}
